import styled from "styled-components";

export const Animation = styled.div`
  .anim {
    transition: 0.65s;
    overflow: hidden;
    width: 100%;
    z-index: 1000;
  }
  .ani {
    transition: 0.65s;
    position: fixed;
    overflow: hidden;
    width: 100%;
    z-index: 1000;
  }
`;
